// ** Reactstrap Imports
// ** React Imports
import { useEffect, useState } from 'react'
import ReactPaginate from 'react-paginate'
// ** Reactstrap Imports
import Avatar from '@components/avatar'
import {
    Row,
    Col,
    Card,
    Form,
    Label,
    Input,
    Button,
    CardBody,
    FormFeedback,
    Table,
    Modal,
    CardTitle,
    ModalHeader,
    Badge,
    ModalBody,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from 'reactstrap'


import '@styles/react/libs/flatpickr/flatpickr.scss'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import * as FileSaver from "file-saver"
import * as XLSX from "xlsx"

import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { Edit, Trash2, Plus, MoreVertical, UploadCloud, Eye } from 'react-feather'
import { Link } from 'react-router-dom'

const PasswordManage = () => {

    const MySwal = withReactContent(Swal)
    const [data, setData] = useState([])

    const [selected, setSelected] = useState(null)
    const [total, setTotal] = useState(1)
    const [currentPage, setCurrentPage] = useState(1)
    const [rowsPerPage, setRowsPerPage] = useState(50)
    const [searchvalue, setSearchvalue] = useState('')

    const renderClient = name => {
        return (
          <Avatar
            initials
            className='me-1'
            color={'light-primary'}
            content={name ? name?.charAt(0).toUpperCase() : ''}
          />
        )
      }

    const getLicenses = (params) => {

        axios.post('/passwordmanage/list', params)
            .then((res) => {
                setData(res.data.passwordmanage.passwordmanage)
                setTotal(res.data.passwordmanage.total)
            })
            .catch((err) => { console.log(err) })
    }

    const handlePerPage = async e => {

        const value = parseInt(e.currentTarget.value)
        setRowsPerPage(value)
        const params = {


            perPage: value,
            page: currentPage,
            q: searchvalue
        }
        getLicenses(params)

    }

    useEffect(() => {

        const params = {


            perPage: rowsPerPage,
            page: currentPage,
            q: searchvalue
        }
        getLicenses(params)

    }, [])

    const handleFilter = (val) => {
        setSearchvalue(val)
        const params = {


            perPage: rowsPerPage,
            page: currentPage,
            q: val
        }
        getLicenses(params)
    }

    const [showModal, setShowModal] = useState(false)
    const openShowModal = (item) => {
        setSelected(item)
        setShowModal(true)
    }


    const handlePagination = (page) => {
        setCurrentPage(page.selected + 1)
        const params = {


            perPage: rowsPerPage,
            page: page.selected + 1,
            q: searchvalue
        }
        getLicenses(params)
    }

    const CustomPagination = () => {
        const count = Number(Math.ceil(total / rowsPerPage))

        return (
            <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                pageCount={count || 1}
                activeClassName='active'
                forcePage={currentPage !== 0 ? currentPage - 1 : 0}
                onPageChange={page => handlePagination(page)}
                pageClassName={'page-item'}
                nextLinkClassName={'page-link'}
                nextClassName={'page-item next'}
                previousClassName={'page-item prev'}
                previousLinkClassName={'page-link'}
                pageLinkClassName={'page-link'}
                containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
            />
        )
    }

    const exportExcel = () => {

        const params = {


            perPage: rowsPerPage,
            page: currentPage,
            q: searchvalue,
            export: true
        }
        axios.post('/passwordmanage/list', params)
            .then(async (res) => {

                const result = await res.data.passwordmanage.passwordmanage.map((obj) => {

                    const data = {}
                    data['client'] = obj.contactname
                    data['name'] = obj.name
                    data['link'] = obj.link
                    data['username'] = obj.username
                    data['password'] = obj.password
                    data['remarks'] = obj.remarks

                    return data
                })

                const ws = await XLSX.utils.json_to_sheet(result, { origin: 'A2', skipHeader: true })
                const Heading = [['Client', 'Name', 'Link', 'Username', 'Password', 'Remarks']]
                XLSX.utils.sheet_add_aoa(ws, Heading)
                const wb = { Sheets: { data: ws }, SheetNames: ["data"] }
                const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" })
                const data = new Blob([excelBuffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8" })
                FileSaver.saveAs(data, 'passwords.xlsx')

            })
            .catch((err) => { console.log(err) })

    }

    return (
        <Card>
            <CardBody >
                <Row className='d-flex justify-content-between px-1 px-lg-0'>
                    <Col xl='3' md='3' sm='3' className='d-flex align-items-center p-0'>
                        <div className='d-flex align-items-center w-100'>
                            <Input
                                className='mx-50'
                                type='select'
                                id='rows-per-page'
                                value={rowsPerPage}
                                onChange={handlePerPage}
                                style={{ width: '5rem' }}
                            >
                                <option value='10'>10</option>
                                <option value='25'>25</option>
                                <option value='50'>50</option>
                            </Input>
                            <label htmlFor='rows-per-page'>Entries</label>
                        </div>
                       
                    </Col>                                  
                    <Col className='d-flex align-items-sm-center justify-content-xl-end justify-content-end flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-0'>
                        <Col className='col-md-3 me-1 mt-50 mt-sm-0 '>
                            <Input
                                id='search-invoice'
                                className='me-1'
                                type='text'
                                placeholder='Search'
                                value={searchvalue}
                                onChange={e => handleFilter(e.target.value)}
                            />
                        </Col>
                        <Col className='d-flex mt-50 mt-lg-0' md={1} sm={12}>

                            <UncontrolledDropdown className='ms-1'>
                                <DropdownToggle color='secondary' caret outline className='px-1'>
                                    <MoreVertical className='font-small-4' />
                                </DropdownToggle>
                                <DropdownMenu>
                                    <DropdownItem className='w-100' tag={Link} to='/import/passwordmanage'>
                                        <UploadCloud className='font-small-4 me-50' />
                                        <span className='align-middle'>Import</span>
                                    </DropdownItem>
                                    <DropdownItem className='w-100' onClick={() => { exportExcel() }}>
                                        <UploadCloud className='font-small-4 me-50' />
                                        <span className='align-middle'>Export</span>
                                    </DropdownItem>
                                </DropdownMenu>
                            </UncontrolledDropdown>
                        </Col>
                    </Col>
                </Row>
            </CardBody>
            <Table className='text-nowrap text-center border-bottom' responsive>
                <thead>
                    <tr>
                        <th className='text-start p-50'>Client</th>
                        <th className='text-start p-50'>Name</th>
                        <th>Link</th>
                        <th>username</th>
                        <th>Password</th>
                        <th>Remarks</th>
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {data.map((item, index) => {
                        return (
                            <tr key={index}>                              
                                <td className='text-start p-50'>
                                    {renderClient(item.contactname)}
                                    {item.contactname?.length > 20 ? `${item.contactname?.substr(0, 20)}...` : item.contactname}</td>
                                <td className='text-start p-50'>{item.mastername}</td>
                                <td className='text-start p-50'>
                                    <a href={item.link} target='_blank'>{item.passwordlink.length > 35 ? `${item.passwordlink.substr(0, 35)}...` : item.passwordlink}</a>
                                </td>
                                <td className='text-start p-50'>
                                    {item.username.length > 15 ? `${item.username.substr(0, 15)}...` : item.username}
                                </td>
                                <td className='text-start p-50'>
                                    {item.password}
                                </td>
                                <td className='text-start p-50'>
                                    <span> {item.remarks.length > 20 ? `${item.remarks.substr(0, 20)}...` : item.remarks}</span>
                                </td>
                                <td>
                                    <div className='column-action d-flex align-items-center'>

                                        <Col
                                            onClick={() => { openShowModal(item) }}>
                                            <Eye className='cursor-pointer ms-25 mt-0' size={16} />
                                        </Col>
                                    </div>
                                </td>
                            </tr>
                        )
                    })}
                </tbody>
            </Table>
            <CustomPagination />

            <Modal isOpen={showModal} toggle={() => setShowModal(!showModal)} className='modal-dialog-centered'>
                <ModalHeader toggle={() => setShowModal(false)}>Password Manager</ModalHeader>
                <ModalBody>
                    <Card>

                        <ul className='list-unstyled'>
                            <li className='mb-75 row'>
                                <span className='fw-bolder col-6'>Client:</span>
                                <span className='col-6 text-end'>{selected?.contactname}</span>
                            </li>
                            <li className='mb-75 row'>
                                <span className='fw-bolder col-6'>Name:</span>
                                <span className='col-6 text-end'>{selected?.mastername}</span>
                            </li>
                            <li className='mb-75 row'>
                                <span className='fw-bolder col-6'>Link:</span>
                                <span className='col-6 text-end'>
                                    <a href={selected?.link} target='_blank'>{selected?.passwordlink}
                                    </a></span>
                            </li>
                            <li className='mb-75 row'>
                                <span className='fw-bolder col-6'>UserName:</span>
                                <span className='col-6 text-end'>{selected?.username}</span>
                            </li>
                            <li className='mb-75 row'>
                                <span className='fw-bolder col-6'>Password:</span>
                                <span className='col-6 text-end'>{selected?.password}</span>
                            </li>
                            <li className='mb-75 row'>
                                <span className='fw-bolder col-6'>Remarks:</span>
                                <p className='col-6 text-end white-space-wrap'>{selected?.remarks}</p>
                            </li>
                        </ul>

                    </Card>
                </ModalBody>
            </Modal>
        </Card>
    )
}

export default PasswordManage
